}
a#direct-download-tab
{
    padding: auto 5rem;
    background: #3772FF !important;
    color:white !important;
}
.tabs-govco .nav-tabs .nav-item 
{
    span, a{
        background: #E5EEFB !important;
        padding: 0.5rem 5rem;
        cursor: pointer;
    }
    &.react-tabs__tab--selected
    {
        a, span{
            background: #3772FF !important;
            color:white !important;
        }
    }

}

.container{

    .item-list{
        border-bottom: #e5edfb 1px solid;
        padding: 0 0.5rem;
        &:last-child{
            border-bottom: none;
        }
        h3{
            a{
                color: #004884;
            }
        }
        &:hover {
            background: #eff3f9;
            h3, p, .govco-icon::before{
                color: #004884;
            }
        }
        p{
            margin: 0px;
        }
    }
    #collapseSimple{
        .card-body{
            padding:1.25rem 0.75rem;
            .item-list{
                padding:1.25rem 0.75rem;
            }
        }
    }
}


.toast.toast-check {
    border: #069169 1px solid;
    border-radius: 10px;
    button.toast-close-button {
        margin-right: 18px;
        color: #059169;
        font-weight: bold;
        font-size: 30px;
    }
    .toast-title {
        text-align: center;
        margin: 0 auto;
        width: 100%;
        span{
            font-size: 3rem;
            display: inline-flex;
            vertical-align: middle;
        }
        h3{
            padding-left: 2rem;
            display: inline-flex;
            font-size: 1.5rem;
            font-weight: normal;
            vertical-align: middle;
            color: #069169;
        }
    }
}

.tab-description{
    padding: 1rem;
    p{
        font-size: 0.95rem;
        strong{
            font-size: 0.95rem;
        }
    }
}
.dropdown__indicator
{
    svg {
        background: #004884;
        fill: white;
        display: inline-block;
        line-height: 1;
        stroke: white;
        stroke-width: 0;
        height: 25px;
        width: 25px;
        border-radius: 5px;
    }
}

.dropdown__menu{
    padding: 0 5%;
    background-color: #E5EEFB !important;
    .dropdown__menu-list{
        background: #E5EEFB;
        .dropdown__option
        {
            width: 100%;
            border-bottom: #0B457F 1px solid;
        }
    }
}


.dropdown__indicator-separator
{
    background:none;
    background-color: #e5edfb;
}
.dropdown__control {
    color: #0B457F !important;
    border-bottom-left-radius: 0 !important;
    border-bottom-right-radius: 0 !important;
    border-top-right-radius: 0.3rem !important;
    border-top-left-radius: 0.3rem !important;

    background-color: #E5EEFB !important;
    border: unset !important;
    border-bottom: 1px solid #004884 !important;
    line-height: 1.4 !important;
}
.input-document{
    color:#0B457F !important;
    padding: 0.575rem .5rem;
    height: 42px;
}
.MuiTableSortLabel-icon {
    opacity: 1!important;
}
th.MuiTableCell-root.MuiTableCell-head.MuiTableCell-alignLeft:last-child {

    text-align: center;
    .MuiTableSortLabel-icon{
        display: none;
    }
}

.wrapper-title-subdownloads{
    padding-left: 17px;
    padding: 1.5rem 1rem 0.5rem;
