        font-size: 1rem;
    }
}

.MuiPaper-root{
    z-index: 1;
    display: contents;
    .MuiPaper-rounded{
        box-shadow:none !important;
    }
    .MuiToolbar-root.MuiToolbar-regular.MuiToolbar-gutters {
