                    position: relative;
                    z-index: 3;
                    height: 50px;
                    background: #fff;
                    border: 2px solid #e5eefb;
                    color: #63717f;
                    background-position: left 10px center;
                    background-repeat: no-repeat;
                    //background: #FFFFFF 0% 0% no-repeat padding-box;
                    box-shadow: 0px 3px 6px #00000029;
                    border-radius: 24px;
                    opacity: 1;
                    border: unset;
                    font-size: 1.2rem;
                    margin-bottom: 40px;
                    text-align: left;
                    font-weight: normal;
                    font-size: 16px/24px;
                    font-family: "Work Sans", sans-serif;
                    letter-spacing: 0;
                    padding-left: 25px;
                    outline:none;

                    &::placeholder{
                        color: #3D4B58;
                        opacity: 0.5;
                    }
                }
                .content-search{
                    outline: none;
                    background: transparent;
                    border: 0;
                    display: block;
                    position: absolute;
                    top: 2px;
                    z-index: 10;
                    right: -5px;
                    line-height: normal;
                    .govco-icon {
                        font-size: 2.65rem;
                    }
                }
            }
        }
        .content-entities-results{
            padding: 25px 25px 40px 25px;
            h3{
                text-align: left;
                font-weight: Bold;
                font-family: 'Montserrat', sans-serif;
                font-size: 20px/21px;
                letter-spacing: 0;
                color: #0B457F;
                padding: 0px;
                margin: 15px 0;
            }
            .btn-link-external{
                text-align: left;
                text-decoration: underline;
                font-weight: normal;
                font-family: 'Work Sans', sans-serif;
                font-size: 16px/21px;
                letter-spacing: 0;
                color: #0B457F;
            }
        }
    }
}

